.logo {
  width: 80%;
  align-self: center;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.app-btn {
  width: 45%;
  max-width: 160px;
  color: #fff;
  margin: 20px 10px;
  text-align: left;
  border-radius: 5px;
  text-decoration: none;
  font-family: "Lucida Grande", sans-serif;
  font-size: 10px;
  text-transform: uppercase;
  box-sizing: border-box;
}
.app-btn.blu {
  background-color: #101010;
  transition: background-color 0.25s linear;
}
.app-btn.blu:hover {
  background-color: #454545;
}
.app-btn i {
  width: 20%;
  text-align: center;
  font-size: 28px;
  margin-right: 7px;
}
.app-btn .big-txt {
  font-size: 17px;
  text-transform: capitalize;
}
.app-btn:hover {
  text-decoration: none;
  color: #fff;
}
.text {
  margin-top: 5px;
}
@keyframes logoAnimation {
  from {
    top: -70vh;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
