footer {
  margin-top: 20vh;
  padding: 25px 20px;
  border-top: solid 1px rgba(0, 0, 0, 0.05);
}
footer p {
  color: rgba(0, 0, 0, 0.3);
  font-size: 16px;
  /* font-weight: bold; */
}

footer a:hover {
  text-decoration: none;
}
footer a {
  margin: 3px auto;
  font-size: 15px;
  /* font-weight: bold; */
}
footer div {
  margin-bottom: 30px;
}
footer.footer {
  background-color: rgb(245, 245, 245);
}
