.loginAdmin {
  margin-top: 10vh;
  border-radius: 5px;
  border: solid 1px rgba(0, 0, 0, 0.1);
  padding: 10px;
  margin-bottom: 12vh;
}
.loginAdmin legend {
  font-size: 30px;
  margin-bottom: 20px;
}

.loginAdmin button {
  background-color: #880b8f;
  border: solid 1px #880b8f;
  margin-top: 20px;
  transition-property: background-color color;
  color: #fff;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}
.loginAdmin button:hover {
  background-color: #fff;
  color: #880b8f;
}
