.contact-form {
  margin-top: 10vh;
  margin-bottom: 10vh;
  border: solid 1px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 4px;
}
.contact-form button {
  background-color: #880b8f;
  border: solid 1px #880b8f;
  margin-top: 20px;
  transition-property: background-color color;
  color: #fff;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}
.contact-form button:hover {
  background-color: #fff;
  color: #880b8f;
}
.contact-form legend {
  font-size: 30px;
  margin-bottom: 20px;
}
