.vendor-admin-page ion-icon {
  margin-right: 5px;
  margin-top: 3px;
  font-size: 30px;
}
.columns-vendor {
  display: flex;
  flex-direction: column;
  align-items: center !important;
}
button ion-icon {
  font-size: 20px;
}
.map {
  width: 100%;
  height: 30vh;
  margin: 10px auto;
  background-color: black;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}
.right {
  margin-top: 10vh;
}
.media-stars ion-icon {
  font-size: 20px;
  color: rgb(255, 217, 0);
  margin-right: 0px !important;
}
.media-stars {
  padding-left: 11px;
  margin-bottom: 4px;
}
.btn {
  font-size: 12px;
  font-weight: bold;
}
.btn-primary {
  background-color: rgb(159, 24, 185);
  border-color: rgb(159, 24, 185);
}
.btn-primary:hover {
  background-color: rgb(255, 255, 255);
  border-color: rgb(159, 24, 185);
  color: rgb(159, 24, 185);
}

.btn-danger {
  background-color: rgb(255, 0, 0);
  border-color: rgb(255, 0, 0);
}
.btn-danger:hover {
  background-color: rgb(255, 255, 255);
  border-color: rgb(255, 0, 0);
  color: rgb(255, 0, 0);
}
