html {
  position: relative;
  min-height: 100%;
}

.nav-item .nav-link:hover {
  color: rgb(199, 4, 183) !important;
}
.nav-item .nav-link {
  transition: all 0.2s ease-in-out;
  font-weight: 500;
}
.nav-link-collapse:after {
  float: right;
  content: "\f067";
  font-family: "FontAwesome";
}

.nav-link-show:after {
  float: right;
  content: "\f068";
  font-family: "FontAwesome";
}

.nav-item ul.nav-second-level {
  padding-left: 0;
}

.nav-item ul.nav-second-level > .nav-item {
  padding-left: 20px;
}

@media (min-width: 992px) {
  .sidenav {
    position: fixed;
    top: 0;
    left: 0;
    width: 300px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.07);
  }

  .navbar-expand-lg .sidenav {
    flex-direction: column;
  }
  .notifications {
    margin-left: 17px;
    margin-top: 10px;
  }
  .navbar {
    height: 0;
  }
  .content-wrapper {
    margin-left: 300px;
    margin-top: 20px;
    margin-bottom: 0;
  }
}
@media (max-width: 991px) {
  .navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    box-shadow: 3px 0px 5px rgba(0, 0, 0, 0.07);
  }
  .notifications {
    margin-left: 12px;
    margin-top: 10px;
  }
  .nav-logo {
    display: none;
  }
}
